<template>
  <v-app style="background: #f0f0f1">
    <v-alert v-if="unsavedChanges" dense type="warning">
      Unsaved changes. Please hit the "Save Changes" button to save
      them.</v-alert
    >
    <v-overlay opacity="0.8" :value="overlay"></v-overlay>
    <v-tabs centered style="background: #f0f0f1">
      <v-tab> Rules </v-tab>
      <v-tab> Service List </v-tab>
      <v-tab> How it works </v-tab>
      <v-tab-item style="background: #f0f0f1">
        <ServicesSelect @unsavedchanged="unsavedChanges = true" />
      </v-tab-item>
      <v-tab-item style="background: #f0f0f1">
        <ServicesOverview />
      </v-tab-item>
      <v-tab-item style="background: #f0f0f1">
        <ServicesHowItWorks @unsavedchanged="unsavedChanges = true" />
      </v-tab-item>
    </v-tabs>
  </v-app>
</template>

<script>
import ServicesSelect from "./components/ServicesSelect.vue";
import ServicesOverview from "./components/ServicesOverview.vue";
import ServicesHowItWorks from "./components/ServicesHowItWorks.vue";
import { isActivated } from "./utils/wpAPI";

export default {
  name: "App",
  components: {
    ServicesSelect,
    ServicesOverview,
    ServicesHowItWorks,
  },
  mounted() {
    if (isActivated()) {
      this.overlay = false;
      return;
    }
    this.overlay = true;
  },
  data: () => ({
    overlay: false,
    unsavedChanges: false,
  }),
};
</script>
