<template>
  <v-container>
    <v-row>
      <v-col align="center"><div class="text-h6">Service</div></v-col>
      <v-col align="center"
        ><div class="text-h6">Allowed with consent</div></v-col
      >
      <v-col cols="1"><v-spacer /></v-col>
    </v-row>
    <v-row
      v-for="(currentSetting, index) in currentServicesSettings"
      :key="index"
    >
      <v-col>
        <v-autocomplete
          v-model="currentSetting.serviceId"
          :items="serviceItems"
          :rules="[]"
          filled
          solo
          hide-selected
          @change="saveNewValues(currentSetting.serviceId)"
        ></v-autocomplete>
      </v-col>
      <v-col>
        <v-autocomplete
          v-model="currentSetting.allowedWithConsents"
          :items="consentCategories"
          filled
          multiple
          solo
          @change="saveNewValues(currentSetting.allowedWithConsents)"
        ></v-autocomplete>
      </v-col>
      <v-col cols="1">
        <v-icon class="pt-4" small @click="deleteItem(index)">
          mdi-delete
        </v-icon>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <v-btn
          elevation="0"
          small
          outlined
          raised
          @click="addItem()"
          color="#2271b1"
        >
          Add a Rule
        </v-btn></v-col
      >
    </v-row>
  </v-container>
</template>

<script>
import {
  getCurrentBlockedServicesSettings,
  getBuiltInServiceList,
  getAvailableConsentCategories,
  saveSettings,
} from "../utils/wpAPI";

export default {
  name: "ServicesSelect",
  components: {},
  data() {
    return {
      currentServicesSettings: [],
      availableServiceList: [],
      availableConsentCategories: [],
      choosenCategorie: "",
      rules: {
        min: (v) => v.length >= 8 || "Min 8 characters",
      },
    };
  },
  computed: {
    chosenServicesId() {
      return this.currentServicesSettings.map((item) => {
        return item.serviceId;
      });
    },
    serviceItems() {
      return this.availableServiceList.map((item) => {
        return { text: item.serviceName, value: item.serviceId };
      });
    },
    consentCategories() {
      return this.availableConsentCategories.map((item) => {
        return { text: item.name, value: item.nameId };
      });
    },
  },
  mounted() {
    this.refreshValues();
  },
  methods: {
    refreshValues() {
      this.currentServicesSettings = getCurrentBlockedServicesSettings();
      this.availableServiceList = getBuiltInServiceList();
      this.availableConsentCategories = getAvailableConsentCategories();
    },
    testAllFieldsFilled() {
      for (let i = 0; i < this.currentServicesSettings.length; i += 1) {
        if (
          !this.currentServicesSettings[i].serviceId ||
          !this.currentServicesSettings[i].allowedWithConsents ||
          this.currentServicesSettings[i].allowedWithConsents.length === 0
        ) {
          return false;
        }
      }
      return true;
    },

    deleteItem(key) {
      delete this.currentServicesSettings[key];
      this.currentServicesSettings = this.currentServicesSettings.filter(
        Boolean
      );
      this.saveNewValues();
    },
    addItem() {
      this.currentServicesSettings.push({
        allowedWithConsents: [],
        serviceId: "",
      });
    },
    saveNewValues() {
      if (!this.testAllFieldsFilled()) {
        return;
      }
      saveSettings(this.currentServicesSettings, "currentServicesSettings");
      this.$emit("unsavedchanged", true);
    },
  },
};
</script>