function getElementForBlockedServices() {
  return window.parent.document.getElementById("ff_nsc_bar_blocked_services");
}

function getCurrentBlockedServicesSettings() {
  const element = getElementForBlockedServices();
  if (!element) {
    console.warn("element not found");
    return [];
    // return [
    //   {
    //     serviceId: "googleAnalytics",
    //     allowedWithConsents: ["cookieconsent_status_tech"],
    //   },
    //   { serviceId: "gtm", allowedWithConsents: ["cookieconsent_status_marketing"] },
    // ];
  }

  const defaultVal = [
    {
      allowedWithConsents: [],
      serviceId: "",
    },
  ];

  let currentServiceSettings;
  try {
    currentServiceSettings = JSON.parse(element.value) || defaultVal;
  } catch (err) {
    currentServiceSettings = defaultVal;
  }

  return currentServiceSettings;
}
// input: {"custqweqweqwe":{"serviceName":"qweqweqwe","regExPattern":"connect.facebook.net|www.facebook.com\/tr","type":"Custom","serviceId":"custqweqweqwe"}}
async function saveCustomServices(newSettings) {
  await callWpAPI("beautiful-and-responsive-cookie-consent/v1/customServices", "POST", newSettings);
}

function saveSettings(newSettings, type) {
  let element;
  if (type === "currentServicesSettings") {
    if (!Array.isArray(newSettings)) {
      console.warn(`saveSettings: no array submitted!`, newSettings);
      return false;
    }
    element = getElementForBlockedServices();
  }

  if (!element) {
    console.error(
      `needed element for type: ${type} not found. This would have been stored:`,
      newSettings
    );
    return false;
  }
  element.value = JSON.stringify(newSettings);
}

function getAvailableConsentCategories() {
  const avalConsentsCats = window.parent.nsc_bara_available_consent_categories;
  if (!avalConsentsCats) {
    console.warn(`no consent cats found. will return fallback!`);
    return [];
    // return [
    //   { nameId: "cookieconsent_status_tech", name: "Technical" },
    //   { nameId: "cookieconsent_status_marketing", name: "Marketing" },
    // ];
  }

  return avalConsentsCats;
}

function isActivated() {
  const checkbox = window.parent.document.getElementById("ff_nsc_bar_activate_service_blocking");
  if (!checkbox || !checkbox.checked || checkbox.disabled) {
    return false;
  }
  return true;
}

