async function getCustomServiceList() {
  let response = await callWpAPI(
    "beautiful-and-responsive-cookie-consent/v1/customServices",
    "GET",
    undefined
  );
  console.log(response);
  if (response.status !== 200) {
    console.error(`no customservices found.Need to abort!`);
    return false;
  }

  const customServices = await response.json();
  console.log(customServices);

  const keys = Object.keys(customServices);
  const customServicesArray = [];
  for (let i = 0; i < keys.length; i += 1) {
    customServicesArray.push(customServices[keys[i]]);
  }
  return customServicesArray;
}

async function callWpAPI(url, method, body) {
  const myHeaders = new Headers();
  myHeaders.append("Content-Type", "application/json");
  myHeaders.append("_wpnonce", window.nonce);
  myHeaders.append("X-WP-Nonce", window.nonce);

  const requestOptions = {
    method: method,
    headers: myHeaders,
    redirect: "follow",
  };

  if (requestOptions.method === "POST") {
    const raw = JSON.stringify(body);
    requestOptions.body = raw;
  }

  const response = await fetch(`${window.restURL}${url}`, requestOptions);

  return response;
}

