<template>
  <v-container>
    <v-card>
      <v-card-title>
        <v-text-field
          v-model="search"
          append-icon="mdi-magnify"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
      </v-card-title>
      <v-data-table
        :headers="headers"
        :items="services"
        :search="search"
        :sort-by="['type', 'serviceName']"
        :sort-desc="[true, false]"
        :footer-props="{
          itemsPerPageOptions: [10, 20, 50, 100, -1],
        }"
      >
        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title>Services</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-dialog v-model="dialog" max-width="500px">
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  color="primary"
                  dark
                  class="mb-2"
                  v-bind="attrs"
                  v-on="on"
                >
                  Add Service
                </v-btn>
              </template>
              <v-card>
                <v-card-title>
                  <span class="headline">{{ formTitle }}</span>
                </v-card-title>

                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.serviceName"
                          label="Service Name"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.regExPattern"
                          label="Regex"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                  </v-container>
                  <v-alert v-if="dialogError" dense type="error">
                    {{ dialogError }}
                  </v-alert>
                  <v-alert dense outlined type="info">
                    Be careful! Use this function only if you know what you are
                    doing and test your settings on a test environment first!
                  </v-alert>
                </v-card-text>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="close">
                    Cancel
                  </v-btn>
                  <v-btn color="blue darken-1" text @click="save"> Save </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
            <v-dialog v-model="dialogDelete" max-width="500px">
              <v-card>
                <v-card-title class="headline"
                  >Are you sure you want to delete this item?</v-card-title
                >
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="closeDelete"
                    >Cancel</v-btn
                  >
                  <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                    >OK</v-btn
                  >
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <!-- <template v-slot:[`item.actions`]="{ item }">
          <v-icon small class="mr-2" @click="editItem(item)">
            mdi-pencil
          </v-icon>
          <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
        </template>
        <template v-slot:no-data>
          <v-btn color="primary" @click="initialize"> Reset </v-btn>
        </template>-->
      </v-data-table>
    </v-card>
  </v-container>
</template>
<script>
import {
  getBuiltInServiceList,
  getCustomServiceList,
  saveCustomServices,
} from "../utils/wpAPI";
export default {
  name: "ServicesOverview",
  components: {},
  data() {
    return {
      search: "",
      headers: [
        {
          text: "Service Name",
          align: "start",
          value: "serviceName",
        },
        { text: "Identifier Regex", value: "regExPattern" },
        { text: "Type", value: "type" },
      ],
      services: getBuiltInServiceList(),
      customServices: false,
      dialogError: "",
      dialog: false,
      dialogDelete: false,
      formTitle: "Add a new service",
      editedIndex: -1,
      defaultItem: {
        serviceName: "",
        regExPattern: "",
        type: "Custom",
      },
      editedItem: {
        serviceName: "",
        regExPattern: "",
        type: "Custom",
      },
    };
  },
  computed: {
    customServicesIds() {
      if (!this.customServices) {
        return false;
      }
      return this.customServices.map((item) => {
        return item.serviceId;
      });
    },
  },
  async mounted() {
    this.customServices = await getCustomServiceList();
    console.log(this.customServices);
  },
  methods: {
    editItem(item) {
      this.editedIndex = this.customServices.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.customServices.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.customServices.splice(this.editedIndex, 1);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.dialogError = "";
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.dialogError = "";
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      if (!this.editedItem.serviceName || !this.editedItem.regExPattern) {
        this.dialogError = `Please provide a value for regex and service name.`;
        return;
      }

      if (!this.customServices) {
        this.dialogError = `There was a Problem getting the configured services. Maybe a reaload of the page helps.`;
        return;
      }

      if (!this.editedItem.serviceId) {
        const newServiceId = `cust${this.editedItem.serviceName.replace(
          /[^A-Za-z0-9]/g,
          ""
        )}`;
        if (this.customServicesIds.includes(newServiceId)) {
          this.dialogError =
            "Service name already taken. Please choose a different one.";
          return;
        }
        this.editedItem.serviceId = newServiceId;
      }
      if (this.editedIndex > -1) {
        Object.assign(this.customServices[this.editedIndex], this.editedItem);
      } else {
        // have to store in both, because services are custom and built in merged from DB, customServices is only need to store custom services updates. Is not used for display.
        this.services.push(this.editedItem);
        this.customServices.push(this.editedItem);
      }

      const customServicesObj = {};
      for (let i = 0; i < this.customServices.length; i += 1) {
        customServicesObj[this.customServices[i].serviceId] =
          this.customServices[i];
      }

      saveCustomServices(customServicesObj, "customServices");
      this.close();
    },
  },
};
</script>